import {mapActions} from 'vuex'
import checkoutPay from '../checkout/checkout-pay'
import checkoutAddressTip from '../checkout/checkout-address-tip'
import checkoutPayWay from '../checkout/checkout-pay-way'
import checkoutGoodsDetails from '../checkout/checkout-goods-details'
export default{
  data () {
    return {
      checkoutId: this.$route.params.id,
      checkoutInfo: {
        address: {}
      }
    }
  },
  components: {
    checkoutPay,
    checkoutAddressTip,
    checkoutPayWay,
    checkoutGoodsDetails
  },
  methods: {
    ...mapActions('cart', ['getCheckoutInfo', 'pay']),

    payHandler () {
      this.pay(this.checkoutId).then(() => {
        this.$go('/transaction/25875', false)
      })
    }
  },
  created () {
    this.getCheckoutInfo(this.checkoutId).then(res => {
      this.checkoutInfo.address = res.address
    })
  },
  beforeRouteLeave (to, from, next) {
    if (to.path === '/cart') {
      this.showDialog(() => {
        next()
      })
    } else {
      next()
    }
  }
}



